@import "scss/settings/variables";
@import "scss/settings/mixins";

footer {
  box-shadow: 0px -5px 40px #00000029;
  height: 89px;
  width: 100vw;
  @include flexCenter;
}

.footer__content {
  @include flexCenter;
  justify-content: space-between;
  width: 100%;
  margin: 0 137px;
}

.footer__logo {
  @include flexColumn;
  &_text {
    font-family: $basic-font-family1;
    font-weight: $font-weight-light;
    font-size: 26px;
    line-height: 30px;
    color: $color-primary;
  }
  &__info {
    font-family: $basic-font-family2;
    font-size: 11px;
    line-height: 15px;
    color: $color-grey;
  }
}

.footer__links {
  display: flex;
  gap: 20px;
  .footer__link1 {
    background-image: url("../../assets/Twitter.svg");
    height: 24px;
    width: 24px;
  }
  .footer__link2 {
    background-image: url("../../assets/Facebook.svg");
    height: 24px;
    width: 24px;
  }
}