@import "scss/settings/variables";
@import "scss/settings/mixins";

.benefits {
  background-color: $-color-light-grey;
  height: 1395px;
  @include flexColumn;
  justify-content: space-evenly;
  align-items: center;
}

.benefits__box {
  width: 1124px;
  height: 370px;
  display: flex;
  justify-content: space-between;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  padding: 91px 35px;
  background-color: $background-primary-color;
  &__box3 {
  background-color: $-color-light-grey;
  border: none;
  padding: 0;
  }
}

.benefits__box__image-container {
  position: relative;
}

.benefits__description {
  width: 40%;
}

.benefits__description_h3 {
  width: 60%;
  font-family: $basic-font-family1;
  font-size: 36px;
  line-height: 42px;
  text-align: left;
}

.h3-box3 {
  margin: 70px 0 30px 35px;
}

.benefits__description_p {
  font-family: $basic-font-family2;
  line-height: $line-height-basic;
  text-align: left;
  width: 80%;
}

.p-box3 {
  margin: 0 36px;
}

.description-box1::after {
  content: "";
  display: block;
  height: 3px;
  background: $color-redish;
  margin: 40px 0 0 0;
  width: 300px;
}

.box1_image {
  width: 386px;
  height: 219px;
  z-index: 1;
  position: absolute;
  top: 9px;
  right: 100px;
}

.benefits__figure1 {
  width: 200px;
  height: 200px;
  background-color: $color-redish;
  border-radius: 41px;
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: absolute;
  z-index: 0;
  top: -39px;
  right: 78px;
}

.box2_image {
  width: 426px;
  height: 328px;
  position: absolute;
  top: -54px;
  left: 3px;
  z-index: 1;
}

.benefits__figure2 {
  width: 320px;
  height: 320px;
  background-color: $color-primary;
  border-radius: $circle-border-radius;
  position: absolute;
  z-index: 0;
  top: -70px;
  left: 3px;
}

.description-box2::after {
  content: "";
  display: block;
  height: 3px;
  background: $color-primary;
  margin: 40px 0 0 0;
  width: 300px;
}

.description-box3 {
  background-color: $background-primary-color;
  width: 623px;
  height: 370px;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  margin: 0;
  &::after {
    content: "";
    display: block;
    height: 3px;
    background: $color-green;
    margin: 40px 0 0 36px;
    width: 300px;
  }
}

.image-container {
  width: 440px;
  height: 370px;
  background-color: $color-green;
  @include flexCenter;
  border: 1px solid rgba(232, 233, 237, 1);
  border-radius: 5px;
  .box3_image {
    width: 296px;
    height: 208px;
  }
}
